import { Component } from "@angular/core";
import { CaclService } from "../calc.service";


@Component({
    selector: 'app-result',
    templateUrl: './result.component.html',
    styleUrls: ['./result.component.scss']
})
export class ResultComponent {
    hrs: string = '00';
    mins: string = '00';
    secs: string = '00';

    constructor(private service: CaclService) {

    }
    ngOnInit() {
        this.service.resultValue$.subscribe((val) => { this.hrs = val.hr; this.mins = val.min; this.secs = val.sec });
    }
}
