import { Injectable } from "@angular/core";
import { Subject } from 'rxjs';

@Injectable()
export class CaclService {
    //distance
    public swimInput$ = new Subject<number>();
    public swimUnitSelect$ = new Subject<string>();
    public bikeInput$ = new Subject<number>();
    public bikeUnitSelect$ = new Subject<string>();
    public runInput$ = new Subject<number>();
    public runUnitSelect$ = new Subject<string>();
    //time
    public swimHr$ = new Subject<string>();
    public swimMin$ = new Subject<string>();
    public swimSec$ = new Subject<string>();
    public swimPaceMin$ = new Subject<string>();
    public swimPaceSec$ = new Subject<string>();
    public swimUnitValue$ = new Subject<string>();

    public bikeHr$ = new Subject<string>();
    public bikeMin$ = new Subject<string>();
    public bikeSec$ = new Subject<string>();
    public bikeSpeed$ = new Subject<string>();
    public bikeUnitValue$ = new Subject<string>();

    public runHr$ = new Subject<string>();
    public runMin$ = new Subject<string>();
    public runSec$ = new Subject<string>();
    public runPaceMin$ = new Subject<string>();
    public runPaceSec$ = new Subject<string>();
    public runUnitValue$ = new Subject<string>();

    public resultValue$ = new Subject<{hr: string, min: string, sec: string}>();

    public changeSwimInput(val: number) {
        this.swimInput$.next(val);
    }

    public changeSwimUnitSelect(val: string) {
        this.swimUnitSelect$.next(val);
    }

    public changeBikeInput(val: number) {
        this.bikeInput$.next(val);
    }

    public changeBikeUnitSelect(val: string) {
        this.bikeUnitSelect$.next(val);
    }

    public changeRunInput(val: number) {
        this.runInput$.next(val);
    }

    public changeRunUnitSelect(val: string) {
        this.runUnitSelect$.next(val);
    }
    public changeResult(val: {hr: string, min: string, sec: string}){
        this.resultValue$.next(val);
    }
}